
import HeaderBack from '../Common/HeaderBack'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'


export default {
components: {
myHeader: HeaderBack,
Group,
Cell,
CellBox
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBC_text:"帮助中心",
HBR_icon: " ",
},
}
},
methods: {
signOut() {
this.$vux.toast.show({
type:'warn',
text:'不能退出,就是这么强硬',
width: '16.5em'
});
}
}
}



